.tesla-stats {
  margin: -70px 0 30px;
}

.tesla-stats ul {
  text-align: center;
}

.tesla-stats ul li {
  display: inline-block;
  position: relative;
  width: 130px;
}

.tesla-stats ul li p {
  display: block;
  position: relative;
  padding: 0 18px 0 0;
  font-family: 'RobotoNormal';
  font-weight: normal;
  font-size: 40px;
  text-align: right;
  color: #008dff;
}

.tesla-stats ul li p:after {
  display: block;
  position: absolute;
  top: 8px;
  right: 0;
  content: 'MI';
  font-family: 'RobotoNormal';
  font-weight: normal;
  font-size: 14px;
}

.tesla-stats-icon {
  height: 20px;
  background-repeat: no-repeat;
  background-size: auto 13px;
  background-position: top right;
}

.tesla-stats-icon--60 {
  background-image: url(../../assets/models/60.svg);
}

.tesla-stats-icon--60d {
  background-image: url(../../assets/models/60d.svg);
}

.tesla-stats-icon--75 {
  background-image: url(../../assets/models/75.svg);
}

.tesla-stats-icon--75d {
  background-image: url(../../assets/models/75d.svg);
}

.tesla-stats-icon--90d {
  background-image: url(../../assets/models/90d.svg);
}

.tesla-stats-icon--p100d {
  background-image: url(../../assets/models/p100d.svg);
}

.tesla-stats-animation {
	-webkit-animation: slit-in-horizontal 1.5s 2s ease-out both;
	        animation: slit-in-horizontal 1.5s 2s ease-out both;
}

@-webkit-keyframes slit-in-horizontal {
  0% {
    -webkit-transform: translateZ(-800px) rotateX(90deg);
            transform: translateZ(-800px) rotateX(90deg);
    opacity: 0;
  }
  54% {
    -webkit-transform: translateZ(-160px) rotateX(87deg);
            transform: translateZ(-160px) rotateX(87deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateZ(0) rotateX(0);
            transform: translateZ(0) rotateX(0);
  }
}

@keyframes slit-in-horizontal {
  0% {
    -webkit-transform: translateZ(-800px) rotateX(90deg);
            transform: translateZ(-800px) rotateX(90deg);
    opacity: 0;
  }
  54% {
    -webkit-transform: translateZ(-160px) rotateX(87deg);
            transform: translateZ(-160px) rotateX(87deg);
    opacity: 1;
  }
  100% {
    -webkit-transform: translateZ(0) rotateX(0);
            transform: translateZ(0) rotateX(0);
  }
}


